.carousel__container {
    display: flex;
    vertical-align: center;
    align-items: center;
    background-color: black;
    padding: 30px 0 30px 0;
    height: 500px;
}

.carousel__container-title {
    color: white;
    width: 50%;
    text-align: center;
    font-size: 30px;
    padding: 0 100px 0 100px;
}

.slide__container{
    height: 70%;
    border-radius: 40px;
    overflow: hidden;
    position: relative;
    cursor: pointer;
}

.slide__container > img{
    object-fit: contain;
    transition: all 0.2s ease-in-out;
}

.slide__container:hover > img{
    transform: scale(1.2);
    opacity: 0.7;
}

.slide__desc{
    background-color: black;
    position: absolute;
    bottom: 0;
    width: 70%;
    padding-left: 30px;
    border-top-right-radius:20px;
    opacity: 0.8;
}

.slide__desc > span > a{
    color: white;
    display: block;
    font-size: 22px;
    padding: 20px 0 20px 10px;
    text-decoration: none;
}

.BrainhubCarousel__arrows {
    background-color: white;
    border-radius: 50%;
    margin: 20px;
}

.BrainhubCarousel__arrows > span {
    border-color: black;
}

.BrainhubCarousel__arrows:hover {
    background-color: white !important;
}

.BrainhubCarousel__arrows:hover > span {
    border-color: black !important;
}

@media screen and (max-width: 960) {
    .carousel__container {
        flex-direction: column;
    }
    .carousel__container-title{
        width: 100%;
        text-align: center;
        padding: 50px 0 50px 0;
    }
    .slide__container {
        width: 100%;
    }
}